import api from './api'

$(() => {
  $('#login').click(function () {
    $('.tabs-list li').eq(0).click()
  })
  $('#register').click(function () {
    $('.tabs-list li').eq(1).click()
  })

  $('#logout').click(function() {
    api.usercenter.logout().then(() => {
      window.location.href = '/homeapi/index/home.html'
    })
  })
  var timer = null
  var app = new Vue({
    el: '#loginForm',
    data() {
      return {
        time: 30,
        loginMsg: {
          visible: false,
          type: 'danger',
          text: '',
        },
        registerMsg: {
          visible: false,
          type: 'danger',
          text: '',
        },
        codeText: '短信验证码',
        loginPhone: null,
        loginPwd: null,
        registerPhone: null,
        registerCode: null,
        registerPwd:null,
        registerRepwd: null
      }
    },
    methods: {
      countDone() {
        timer = setInterval(() => {
          if(this.time === 0) {
            clearInterval(timer);
            this.time = 30;
            this.codeText = '短信验证码';
            return
          }
          this.codeText = this.time-- + 's'
        }, 1000);
      },
      toggleMsg(el, text) {
        el.visible = true
        el.text = text
        el = {
          visible: true,
          type: 'danger',
          text: false
        }

        setTimeout(() => {
          this.loginMsg.visible = false
          this.registerMsg.visible = false
        }, 2500)
      },
      login() {
        api.usercenter
        .login(this.loginPhone, this.loginPwd)
        .then((res) => {
          if (res.code!=='0000') {
            this.toggleMsg(this.loginMsg, res.message)
          } else {
            window.localStorage.setItem('token', res.data.token)
            window.location.reload()
          }
        })
      },
      signup() {
        api.usercenter
        .register(this.registerPhone, this.registerCode, this.registerPwd)
        .then((res) => {
          if (res.code!=='0000') {
            this.toggleMsg(this.registerMsg, res.message)
          } else {
            window.localStorage.setItem('token', res.data.token)
            window.location.reload()
          }
        })
      },
      getCode() {
        if(this.codeText!='短信验证码') {
          return
        }
        api.usercenter.sendCode(this.registerPhone).then((res) => {
          if(res.code!=='0000') {
            this.toggleMsg(this.registerMsg, res.message)
          } else {
            this.countDone()
          }
        })
      }
    }
  })
});



